// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
import { Icon, IconifyIcon } from "@iconify/react";

// utils

import { customShadows } from "../../../../../../theme/shadows";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z1,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 16,
  height: 22,
  fontWeight: 500,
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "#343A40",
}));

const ValuePercentage = styled(Typography)(() => ({
  fontSize: 14,
  height: 18,
  fontWeight: "normal",
}));

// ----------------------------------------------------------------------

interface MyWalletIncomeCardProps {
  label: string;
  labelColor: string;
  value: string;
  icon: IconifyIcon;
  iconColor: string;
  pecentage: string;
}
export default function MyWalletIncomeCard({
  label,
  labelColor,
  value,
  icon,
  iconColor,
  pecentage,
}: MyWalletIncomeCardProps) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="flex-start"
        spacing={3}
      >
        <Label sx={{ color: labelColor }}>{label}</Label>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={12}
        >
          <Value>{value}</Value>
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={1}
          >
            <Icon icon={icon} color={iconColor} />
            <ValuePercentage sx={{ color: iconColor }}>
              {pecentage}%
            </ValuePercentage>
          </Stack>
        </Stack>
      </Stack>
    </RootStyle>
  );
}
