import { merge } from "lodash";
import ReactApexChart from "react-apexcharts";
// material
import { Card, CardHeader, Box } from "@material-ui/core";

import BaseOptionChart from "../../../../../../components/BaseOptionChart";

//

// ----------------------------------------------------------------------

export default function DonationsGrowth({ monthly_income }) {
  const CHART_DATA = [
    {
      name: "Income",
      type: "line",
      data: monthly_income[0],
    },
  ];
  const chartOptions = merge(BaseOptionChart(), {
    stroke: { width: 3 },
    plotOptions: { bar: { columnWidth: "11%", borderRadius: 4 } },
    fill: { type: ["solid", "gradient", "solid"] },
    labels: monthly_income[1],
    xaxis: { type: "datetime" },

    tooltip: {
      shared: true,
      intersect: false,
      y: {
        formatter: (y) => {
          if (typeof y !== "undefined") {
            return `${y.toFixed(0)} USD dollars`;
          }
          return y;
        },
      },
    },
  });

  return (
    <Card>
      <CardHeader title="Donations Growth" subheader="(+43%) than last year" />
      <Box sx={{ p: 3, pb: 1 }}>
        <p>USD raised</p>
      </Box>

      <Box sx={{ p: 3, pb: 1 }} dir="ltr">
        <ReactApexChart
          type="line"
          series={CHART_DATA}
          options={chartOptions}
          height={364}
        />
      </Box>
    </Card>
  );
}
