import flagFill from "@iconify/icons-ant-design/flag-fill";

import { Icon } from "@iconify/react";
// material
import { styled } from "@material-ui/core/styles";
import { Card, Typography, Stack, Grid } from "@material-ui/core";
// utils
import { customShadows } from "../../../../../../theme/shadows";
import FlagGreen from "../../../../../../assets/icons/flag_green.png";
import FlagOrange from "../../../../../../assets/icons/flag_orange.png";
import FlagRed from "../../../../../../assets/icons/flag_red.png";
import Flagblue from "../../../../../../assets/icons/flag_blue.png";
// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  boxShadow: customShadows.z24,
  minHeight: 156,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  color: "#6C757D",
}));

const Value = styled(Typography)(() => ({
  fontSize: 40,
  height: 40,
  color: "#343A40",
  fontWeight: "bold",
}));

// ----------------------------------------------------------------------

export default function CardGoals(
  {active_goals, completed_goals, pending_goals, rejected_goals}:any
) {
  const demoData = [
    {
      label: "Active Goals",
      value: active_goals,
      img: FlagGreen,
    },
    {
      label: "Reached Goals",
      value: completed_goals,
      img: Flagblue,
    },
    {
      label: "Pending Goals",
      value: pending_goals,
      img: FlagOrange,
    },
    {
      label: "Inactive Goals",
      value: rejected_goals,
      img: FlagRed,
    },
  ];
  return (
    <RootStyle>
      <Grid
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
        spacing={3}
      >
        {demoData.map((item) => (
          <Grid item md={3}>
            <Stack
              direction="column"
              justifyContent="center"
              alignItems="flex-start"
              spacing={1}
              p={2}
            >
              <img src={item.img} alt="s" style={{ height: 30, width: 26 }} />

              <Label>{item.label}</Label>
              <Value>{item.value}</Value>
            </Stack>
          </Grid>
        ))}
      </Grid>
    </RootStyle>
  );
}
