import { saveLoginAction } from "./../redux/actions/loginAction";
import axios from "axios";
import jwt_decode from "jwt-decode";
import { useDispatch, useSelector } from "react-redux";
import { RootAppState } from "./typeHelpers";
import { API_URL } from "./Api";
import { saveUserInfoAction } from "../redux/actions/saveUserInfoAction";

const useAxios = () => {
  const { isLoggedIn, userInfo } = useSelector(
    (state: RootAppState) => state.AuthReducer
  );

  const dispatch = useDispatch();

  const axiosInstance = axios.create({
    baseURL: API_URL,
    headers: isLoggedIn
      ? {
          Authorization: `Bearer ${userInfo?.access}`,
        }
      : {
          "Content-Type": "application/json",
        },
  });

  if (isLoggedIn) {
    axiosInstance.interceptors.request.use(async (req) => {
      const user: any = jwt_decode(userInfo.access);

      const unixNow = Math.round(new Date().getTime() / 1000);
      const isExpired = unixNow > user.exp;

      if (!isExpired) return req;

      const response = await axios.post(`${API_URL}/token/refresh/`, {
        refresh: userInfo.refresh,
      });

      if (req.headers) {
        req.headers.Authorization = `Bearer ${response.data.access}`;
      }
      // dispatch(saveUserInfoAction(response.data));
      return req;
    });
  }

  return axiosInstance;
};

export default useAxios;
