// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
import { Icon } from "@iconify/react";
import ArrowUp from "@iconify/icons-ant-design/arrow-up-outlined";

import { customShadows } from "../../../../../../theme/shadows";

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z24,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: 600,
  color: "#6C757D",
  width: 201,
  textAlign: "center",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "black",
}));

// ----------------------------------------------------------------------

export default function MoneyRaised({total_30_days_raised}:{total_30_days_raised:any}) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={5}
      >
        <Label>Money raised in last 30 days</Label>
        <Value>${total_30_days_raised}</Value>
      </Stack>
    </RootStyle>
  );
}
