import { styled } from "@material-ui/styles";
import { animated } from "@react-spring/web";

const Main = styled("div")({
  cursor: "pointer",
  color: "#676767",
  WebkitUserSelect: "none",
  userSelect: "none",
  display: "flex",
  alignItems: "center",
  height: "100%",
  justifyContent: "center",
});

const Container = styled("div")({
  position: "fixed",
  zIndex: 1000,
  width: "0 auto",
  top: 30,
  margin: "0 auto",
  left: 5,
  right: 5,
  display: "flex",
  flexDirection: "column",
  pointerEvents: "none",
  alignItems: "center",
});

const Message = styled(animated.div)({
  position: "relative",
  overflow: "hidden",
  width: "40ch",
  boxSizing: "border-box",
});

