const Content = styled("div")({
  color: "white",
  backgroundImage: "linear-gradient(130deg, #ed213a, #f26375)",
  opacity: 0.9,
  padding: 22,
  fontSize: "1em",
  display: "grid",
  gridTemplateColumns: "1fr auto",
  gridGap: 10,
  overflow: "hidden",
  height: "auto",
  borderRadius: 3,
  marginTop: 10,
});

