const Button = styled("div")({
  cursor: "pointer",
  pointerEvents: "all",
  outline: 0,
  border: "none",
  backgroundColor: "transparent",
  display: "flex",
  alignSelf: "flex-end",
  overflow: "hidden",
  margin: 0,
  padding: 0,
  paddingBlock: 14,
  color: "rgba(255, 255, 255, 0.5)",
  "&:hover": {
    color: "rgba(255, 255, 255, 0.6)",
  },
});

const Life = styled(animated.div)({
  position: "absolute",
  bottom: 0,
  left: 0,
  width: "auto",
  backgroundImage: "linear-gradient(130deg, #a8ff78, #78ffd6)",
  height: 5,
});

