import { Icon } from "@iconify/react";
import EyeFilled from "@iconify/icons-ant-design/eye-filled";
// material
import { styled } from "@material-ui/core/styles";
import { Card, Typography, Avatar, Stack } from "@material-ui/core";
// utils
import { fShortenNumber } from "../../../../../../utils/formatNumber.js";
import AvatarMonth from "../../../../../../assets/cards/profileavatar.png";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  boxShadow: "none",
  background: "linear-gradient(180deg, #4CC9F0 0%, #43627D 100%)",
  height: 156,
}));

const DoneOfMonth = styled(Typography)(() => ({
  fontsize: 20,
  height: 24,
  color: "white",
  fontWeight: 600,
}));

const DoneName = styled(Typography)(() => ({
  fontsize: 20,
  height: 24,
  color: "white",
  fontWeight: 600,
}));

const DonePost = styled(Typography)(() => ({
  fontsize: 14,
  height: 18,
  color: "#EEF8FF",
}));

// ----------------------------------------------------------------------

const TOTAL = 10;

export default function ProfileViews() {
  return (
    <RootStyle>
      <Stack
        direction="column"
        alignItems="center"
        justifyContent="center"
        p={2}
      >
        <DoneOfMonth>Done of The Month</DoneOfMonth>
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          spacing={3}
        >
          <Avatar
            src={AvatarMonth}
            alt="AvatarMonth"
            sx={{ width: 65, height: 65 }}
          />
          <Stack
            direction="column"
            alignItems="flex-start"
            justifyContent="center"
            p={2}
          >
            <DoneName>Willy Wonkatá</DoneName>
            <DonePost>{TOTAL} Posts</DonePost>
            <DonePost>600 interactions</DonePost>
          </Stack>
        </Stack>
      </Stack>
    </RootStyle>
  );
}
