import { styled } from "@material-ui/core/styles";
import React, { useState, useRef } from "react";
// material
import {
  Box,
  Grid,
  Card,
  Typography,
  CardContent,
  Avatar,
  Stack,
} from "@material-ui/core";

// utils
import Scrollbar from "../../../../../../components/Scrollbar.js";
import {
  AddFunction,
  DoneeStatusProps,
  EndorsedDoneeProps,
  RootAppState,
} from "../../../../../../utils/typeHelpers";
import { DropDownMenu } from ".";
import { DASHBOARD_DONEE_CHANGE_STATUS_API } from "../../../../../../utils/Api";
import { useSelector } from "react-redux";
import { useMutation, UseMutationResult } from "react-query";
import { MessageError } from "../../../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../../../components/AlertMessageSuccess";

// ----------------------------------------------------------------------

//style
const Label = styled(Typography)(() => ({
  fontSize: 22,
  height: 30,
  fontWeight: "bold",
  color: "#343A40",
  textTransform: "capitalize",
}));

const GoalsNo = styled(Typography)(() => ({
  fontSize: 16,
  height: 23,
  fontWeight: 600,
  color: "#343A40",
}));
const GoalsNoSpan = styled("span")(() => ({
  fontSize: 16,
  height: 23,
  fontWeight: "lighter",
  color: "#343A40",
}));

const DoneActive = styled(Typography)(() => ({
  fontSize: 20,
  height: 26,
}));

// ----------------------------------------------------------------------

function EndorsedItem({
  item,
  handleRefetch,
}: {
  item: EndorsedDoneeProps;
  handleRefetch: () => void;
}) {
  const {
    id,
    total_goal_count,
    full_name,
    is_active,
    total_donee_wallet,
    image,
  } = item;
  const refSucess = useRef<null | AddFunction>(null);
  const refError = useRef<null | AddFunction>(null);
  const [selectedIndex, setSelectedIndex] = useState(0);

  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };
  const handleCreateSuccess = (msg: string) => {
    refSucess.current?.(msg);
  };

  const mutationCreate: UseMutationResult<
    DoneeStatusProps,
    Error,
    DoneeStatusProps
  > = useMutation(async (dataReq: DoneeStatusProps) => {
    const response = await DASHBOARD_DONEE_CHANGE_STATUS_API(
      userState.userInfo.access,
      dataReq
    );
    if (response === "Network response was not ok.") {
      handleCreateError("Network response was not ok.");
    } else {
      handleCreateSuccess("Status Changed");
      return response;
    }
  });

  const OPTIONS = [
    { value: "True", label: "Active" },
    { value: "False", label: "In Active" },
  ];

  const handleStatusChange = async (index: number) => {
    setSelectedIndex(index);
    const data: DoneeStatusProps = {
      profile: id,
      is_active: OPTIONS[index].value,
    };
    mutationCreate.mutateAsync(data);
    handleRefetch();
  };

  return (
    <Grid item xs={12} sm={6} md={4} key={id}>
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />
      <MessageSuccess
        children={(add: AddFunction) => {
          refSucess.current = add;
        }}
      />
      <Stack
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
        spacing={2}
      >
        <Avatar src={image} alt="avatar" sx={{ height: 60, width: 60 }} />
        <Stack
          direction="column"
          justifyContent="flex-start"
          alignItems="flex-start"
          spacing={1}
        >
          <Label>{full_name}</Label>
          <Stack
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
            spacing={1}
          >
            <GoalsNo>
              {total_goal_count} <GoalsNoSpan>Goals</GoalsNoSpan>
            </GoalsNo>
            <GoalsNo>
              {total_donee_wallet} <GoalsNoSpan>Raised</GoalsNoSpan>
            </GoalsNo>
          </Stack>
          <DoneActive sx={{ color: is_active ? "#16B698" : "tomato" }}>
            {is_active ? "Active Donee" : "Inactive Donee"}
          </DoneActive>
        </Stack>
        <DropDownMenu
          options={OPTIONS}
          selectedIndex={selectedIndex}
          handleStatusChange={handleStatusChange}
        />
      </Stack>
    </Grid>
  );
}

export default function EndorsedDonee({
  doneeList,
  handleRefetch,
}: {
  doneeList: EndorsedDoneeProps[];
  handleRefetch: () => void;
}) {
  return (
    <Card>
      <Scrollbar
        sx={{
          height: 350,
          "& .simplebar-content": {
            height: "100%",
            display: "flex",
            flexDirection: "column",
          },
        }}
      >
        <CardContent>
          <Grid container spacing={2}>
            {doneeList.map((item) => (
              <EndorsedItem
                key={item.id}
                item={item}
                handleRefetch={handleRefetch}
              />
            ))}
          </Grid>
        </CardContent>
      </Scrollbar>
    </Card>
  );
}
