import React, { useState } from "react";
import { styled } from "@material-ui/core/styles";
import {
  Avatar,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Card,
  CardHeader,
} from "@material-ui/core";
import { useSelector } from "react-redux";

import {
  CashoutProps,
  FundsProps,
  FundTransferApiResponse,
  RootAppState,
} from "../../../../../../utils/typeHelpers";
import { applySortFilter } from "../Goals";
import { customShadows } from "../../../../../../theme/shadows";
import { CashoutModal, UserListHead, UserListToolbar } from ".";
import Scrollbar from "../../../../../../components/Scrollbar";

import SearchNotFound from "../../../../../../components/SearchNotFound";

function getComparator(order: string, orderBy: keyof FundsProps) {
  return order === "desc"
    ? (a: FundsProps, b: FundsProps) => descendingComparator(a, b, orderBy)
    : (a: FundsProps, b: FundsProps) => -descendingComparator(a, b, orderBy);
}

function descendingComparator(
  a: FundsProps,
  b: FundsProps,
  orderBy: keyof FundsProps
) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

const TABLE_HEAD = [
  { id: "image", label: "Avatar", alignRight: false },
  { id: "title", label: "Title", alignRight: false },
  { id: "paid_amount", label: "Paid Amount", alignRight: false },
  { id: "total_amount", label: "Total Amount", alignRight: false },
  { id: "available_amount", label: "Available Amount", alignRight: false },
  { id: "action", label: "Action", alignRight: false },
];

const FundTransferTable = ({ data }: { data: FundTransferApiResponse }) => {
  const profile_image = useSelector(
    (state: RootAppState) =>
      state.AuthReducer.userInfo.user_profile?.profile_image
  );

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [filterName, setFilterName] = useState("");
  const [order, setOrder] = useState("asc");
  const [open, setOpen] = useState(false);
  const [orderBy, setOrderBy] = useState<keyof FundsProps>("title");
  const [cashOutData, setCashoutData] = useState<CashoutProps>({
    remark: "",
    profile: null,
    goal: null,
  });

  const handleOpen = async (id: number | null) => {
    await setCashoutData({ ...cashOutData, goal: id });
    await setOpen(true);
  };
  const handleClose = () => setOpen(false);
  const handleFilterByName = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFilterName(event.target.value);
  };

  const handleRequestSort = (
    event: React.ChangeEvent<HTMLInputElement>,
    property: keyof FundsProps
  ) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const filteredUsers = applySortFilter(
    data.goals,
    getComparator(order, orderBy),
    filterName
  );

  const isUserNotFound = filteredUsers.length === 0;

  const handleChangePage = (event: any, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <Card sx={{ boxShadow: customShadows.z1 }}>
      <CardHeader title="Fund Transfer Table" />
      <CashoutModal
        open={open}
        handleClose={handleClose}
        goal={cashOutData.goal}
      />
      <UserListToolbar
        filterName={filterName}
        onFilterName={handleFilterByName}
      />

      <Scrollbar sx={{}}>
        <TableContainer sx={{ minWidth: 800 }}>
          <Table>
            <UserListHead
              order={order}
              orderBy={orderBy}
              headLabel={TABLE_HEAD}
              rowCount={data.goals.length}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {filteredUsers
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row: FundsProps) => {
                  const {
                    id,
                    title,
                    image,
                    paid_amount,
                    total_amount,
                    available_amount,
                  } = row;

                  return (
                    <TableRow key={id}>
                      <TableCell align="left">
                        <Avatar src={image} />
                      </TableCell>
                      <GoalName align="left">{title}</GoalName>
                      <TableCell align="left">${total_amount}</TableCell>
                      <TableCell align="left">${paid_amount}</TableCell>
                      <TableCell align="left">${available_amount}</TableCell>
                      <TableCell align="left">
                        <Button
                          disabled={
                            parseInt(available_amount) === 0 ? true : false
                          }
                          onClick={() => handleOpen(id)}
                        >
                          Cashout
                        </Button>
                      </TableCell>
                    </TableRow>
                  );
                })}
              <TableRow>
                <TableCell align="left">
                  <Avatar src={profile_image} />
                </TableCell>
                <GoalName align="left">Donee Amount</GoalName>
                <TableCell align="left"></TableCell>
                <TableCell align="left"></TableCell>
                <TableCell align="left">
                  ${data.ngo_amount_from_donee.ngo_amount_from_donee}
                </TableCell>
                <TableCell align="left">
                  <Button
                    onClick={() => handleOpen(null)}
                    disabled={
                      parseInt(
                        data.ngo_amount_from_donee.ngo_amount_from_donee
                      ) === 0
                        ? true
                        : false
                    }
                  >
                    Cashout
                  </Button>
                </TableCell>
              </TableRow>
            </TableBody>
            {isUserNotFound && (
              <TableBody>
                <TableRow>
                  <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                    <SearchNotFound searchQuery={filterName} />
                  </TableCell>
                </TableRow>
              </TableBody>
            )}
          </Table>
        </TableContainer>
      </Scrollbar>

      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={data.goals.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Card>
  );
};

const GoalName = styled(TableCell)(() => ({
  color: "#343A40",
  fontsize: 14,
  fontWeight: "normal",
  textTransform: "capitalize",
}));

export default FundTransferTable;
