// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
// utils

import goalGreenIcon from "../../../../../../assets/icons/flag_green.png";
import goalBlueIcon from "../../../../../../assets/icons/flag_blue.png";
import { customShadows } from "../../../../../../theme/shadows";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z24,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: "lighter",
  color: "#6C757D",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "black",
}));

// ----------------------------------------------------------------------
interface ActiveInactiveDoneesProps {
  active?: number;
  inactive?: number;
}
const ActiveInactiveDonees: React.FC<ActiveInactiveDoneesProps> = ({
  active,
  inactive,
}) => {
  return (
    <RootStyle>
      <Stack
        direction="row"
        justifyContent="space-around"
        alignItems="center"
        spacing={6}
      >
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
          spacing={1}
        >
          <img src={goalGreenIcon} alt="s" style={{ height: 30, width: 26 }} />
          <Label>Active Donee</Label>
          <Value>{active}</Value>
        </Stack>
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
          spacing={1}
        >
          <img src={goalBlueIcon} alt="s" style={{ height: 30, width: 26 }} />
          <Label>Inactive Donee</Label>
          <Value>{inactive}</Value>
        </Stack>
      </Stack>
    </RootStyle>
  );
};

export default ActiveInactiveDonees;
