import React, { useEffect } from "react";
import { Container, Stack } from "@material-ui/core";

import { useMutation, UseMutationResult } from "react-query";
import {
  FundTransferApiResponse,
  FundTransferHistory,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import useAxios from "../../../../../utils/useAxios";

import Loader from "../../../../../components/Loader";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import { useSelector } from "react-redux";
import FundTransferTable from "./components/FunTransferTable";
import FundTransferHistoryTable from "./components/FunTransferHistorytable";

interface FundtransferReqProps {
  profile: number;
}

const FundTransfer = () => {
  const axiosIntance = useAxios();
  const profileID = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.user_profile?.id
  );

  const fundTransferMutation: UseMutationResult<
    FundTransferApiResponse,
    Error,
    FundtransferReqProps
  > = useMutation(["fundTransferMutation"], async (dataReq) => {
    var formdata = new FormData();
    formdata.append("profile", `${dataReq.profile}`);

    const response = await axiosIntance.post(`/paid-goal-list/`, formdata);
    return response.data;
  });

  const fundTransferHistoryMutation: UseMutationResult<
    FundTransferHistory[],
    Error,
    FundtransferReqProps
  > = useMutation(["fundTransferHistoryMutation"], async (dataReq) => {
    var formdata = new FormData();
    formdata.append("profile", `${dataReq.profile}`);

    const response = await axiosIntance.post(
      `/cashout-history-list/`,
      formdata
    );
    return response.data;
  });

  useEffect(() => {
    if (profileID) {
      fundTransferMutation.mutate({ profile: profileID });
      fundTransferHistoryMutation.mutate({ profile: profileID });
    }
  }, []);

  const { data, isLoading, isError, error } = fundTransferMutation;

  const {
    data: dataHistory,
    isLoading: isLoadingHistory,
    isError: isErrorHistory,
    error: erroHistory,
  } = fundTransferHistoryMutation;

  return (
    <Container maxWidth="xl" sx={{ minHeight: "100vh" }}>
      {isLoading && isLoadingHistory ? (
        <Loader />
      ) : isError ? (
        <ApiErrorPage error={error ? error : new Error("Network Error")} />
      ) : (
        <Stack direction="column" spacing={5}>
          <div>
            {isError ? (
              <ApiErrorPage
                error={error ? error : new Error("Network Error")}
              />
            ) : (
              data && <FundTransferTable data={data} />
            )}
          </div>
          <div>
            {isErrorHistory ? (
              <ApiErrorPage
                error={error ? error : new Error("Network Error")}
              />
            ) : (
              dataHistory && <FundTransferHistoryTable data={dataHistory} />
            )}
          </div>
        </Stack>
      )}
    </Container>
  );
};

export default FundTransfer;
