// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
// utils

import userIcon from "../../../../../../assets/icons/Donees.svg";
import { customShadows } from "../../../../../../theme/shadows";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z24,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: "lighter",
  color: "#6C757D",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "black",
}));

// ----------------------------------------------------------------------

export default function TotalDonee({ total_donee_count }: { total_donee_count?: number }) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="flex-start"
        spacing={1}
      >
        <img src={userIcon} alt="s" style={{ height: 30, width: 30 }} />
        <Label>Donees</Label>
        <Value>{total_donee_count}</Value>
      </Stack>
    </RootStyle>
  );
}
