import faker from "faker";
import PropTypes from "prop-types";
import { Icon } from "@iconify/react";
import { formatDistance } from "date-fns";
import { Link as RouterLink } from "react-router-dom";
import arrowIosForwardFill from "@iconify/icons-eva/arrow-ios-forward-fill";
import { RecentTransaction } from "../../../../../../utils/typeHelpers";
import React, { useState, useEffect } from "react";
// material
import {
  Box,
  Stack,
  Link,
  Card,
  Button,
  Divider,
  Typography,
  CardHeader,
} from "@material-ui/core";

import { styled } from "@material-ui/core/styles";
// utils

import Scrollbar from "../../../../../../components/Scrollbar";
import { fDateTimeComment } from "../../../../../../utils/formatTime";

//

// ----------------------------------------------------------------------

const Title = styled(Typography)(() => ({
  fontSize: 16,
  height: 24,
  fontWeight: 500,
  color: "#000000",
}));

const DateText = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: "normal",
  color: "#CED4DA",
}));

const SendTo = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: "normal",
  color: "#6C757D",
}));
const Value = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: 500,
}));

const NEWS = [...Array(4)].map((_, index) => {
  const sign = ["+", "-"];
  const indexRandom = Math.round(Math.random());
  return {
    title: `${faker.name.firstName()} ${faker.name.lastName()}`,
    to: `${faker.name.firstName()} ${faker.name.lastName()}`,
    postedAt: faker.date.soon(),
    amount: faker.commerce.price(),
    sign: sign[indexRandom],
  };
});

// ----------------------------------------------------------------------

TransactionItem.propTypes = {
  news: PropTypes.object.isRequired,
};

function TransactionItem({ transaction, color }) {
  const { donor, amount, donee, transaction_time } = transaction;

  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-between"
      spacing={0}
      sx={{ backgroundColor: color, padding: 2 }}
    >
      <Box sx={{ minWidth: 200 }}>
        <Link to="#" color="inherit" underline="hover" component={RouterLink}>
          <Title noWrap>{donor}</Title>
        </Link>
        {/* <DateText>{formatDistance(transaction_time, new Date())}</DateText> */}
        <DateText>{fDateTimeComment(transaction_time)}</DateText>
        <SendTo>Donee: {donee}</SendTo>
      </Box>
      <Value
        sx={{
          color: "rgba(22, 182, 152, 0.65)",
          // : "rgba(207, 16, 16, 0.65)",
        }}
      >
        ${amount}
      </Value>
    </Stack>
  );
}

export default function RecentTransactions(recent_transaction) {
  const transaction_data = recent_transaction.recent_transaction;
  const [tableData, setTableData] = useState([
    ["To", "From", "Amount", "Date"],
  ]);

  useEffect(() => {
    if (transaction_data !== undefined && transaction_data.length > 0) {
      transaction_data.map((item) =>
        setTableData((oldData) => [
          ...oldData,
          [
            item.donee,
            item.donor,
            item.amount,
            fDateTimeComment(item.transaction_time),
          ],
        ])
      );
    }
  }, []);

  function exportToCsv() {
    var CsvString = "";
    tableData.forEach(function (RowItem, RowIndex) {
      RowItem.forEach(function (ColItem, ColIndex) {
        CsvString += ColItem + ",";
      });
      CsvString += "\r\n";
    });
    CsvString = "data:application/csv," + encodeURIComponent(CsvString);
    var x = document.createElement("A");
    x.setAttribute("href", CsvString);
    x.setAttribute("download", "recent_transaction.csv");
    document.body.appendChild(x);
    x.click();
  }

  function excelformat() {
    var CsvString = "";
    tableData.forEach(function (RowItem, RowIndex) {
      RowItem.forEach(function (ColItem, ColIndex) {
        CsvString += ColItem + ",";
      });
      CsvString += "\r\n";
    });

    var excel_file = document.createElement("a");
    excel_file.setAttribute(
      "href",
      "data:application/vnd.ms-excel;charset=utf-8," +
        encodeURIComponent(CsvString)
    );
    excel_file.setAttribute("download", "recent_transaction.xls");
    document.body.appendChild(excel_file);
    excel_file.click();
    document.body.removeChild(excel_file);
  }

  return (
    <div>
      <CardHeader title="Recent Transactions" />
      <Scrollbar
        sx={{
          height: 340,
          "& .simplebar-content": {
            height: "100%",
            display: "flex",
            flexDirection: "column",
          },
        }}
      >
        <Button onClick={exportToCsv}>export to CSV</Button>
        <Button onClick={excelformat}>export to Excel</Button>
        <Stack spacing={3} sx={{ p: 3, pr: 0 }}>
          {transaction_data.length > 0 &&
            transaction_data.map((transaction, index) => (
              <div>
                {index % 2 ? (
                  <TransactionItem
                    key={transaction.transaction_time}
                    transaction={transaction}
                    color="transparent"
                  />
                ) : (
                  <TransactionItem
                    key={transaction.transaction_time}
                    transaction={transaction}
                    color="white"
                  />
                )}
              </div>
            ))}
        </Stack>
      </Scrollbar>

      <Divider />

      <Box sx={{ p: 2, textAlign: "center" }}>
        <Button
          to="#"
          size="small"
          color="inherit"
          component={RouterLink}
          sx={{ color: "#007AFF", fontSize: 16 }}
          endIcon={<Icon icon={arrowIosForwardFill} />}
        >
          See More
        </Button>
      </Box>
    </div>
  );
}
