import { Icon } from "@iconify/react";
import EyeFilled from "@iconify/icons-ant-design/eye-filled";
// material
import { styled } from "@material-ui/core/styles";
import { Card, Typography, Stack } from "@material-ui/core";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  padding: 10,
  boxShadow: "none",
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: "lighter",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  paddingTop: 16,
  // height: theme.spacing(8),
}));

// ----------------------------------------------------------------------

const TOTAL = 3021;

export default function ProfileViews({ view_count, bgColor }: { view_count: any, bgColor: string }) {
  return (
    <RootStyle
      sx={{
        backgroundImage: bgColor,
      }}
    >
      <Stack
        direction="column"
        justifyContent="flex-start"
        alignItems="flex-start"
        spacing={1}
      >
        <Icon icon={EyeFilled} width={30} height={30} />
        <Label>Profile Views</Label>
        <Value>{view_count}</Value>
      </Stack>
    </RootStyle>
  );
}
