import { ILoginResponse } from "../../utils/typeHelpers";
import { SAVE_USER_INFO } from "../types";

export const saveUserInfoAction = (userInfo: ILoginResponse) => {
  return (
    dispatch: (arg0: { type: string; payload: ILoginResponse }) => void
  ) => {
    dispatch({
      type: SAVE_USER_INFO,
      payload: userInfo,
    });
  };
};
