import React, { Suspense } from "react";
import { QueryClient, QueryClientProvider } from "react-query";
import { BrowserRouter } from "react-router-dom";
import "simplebar/src/simplebar.css";
import { initializeApp } from "firebase/app";
import ThemeConfig from "./theme";
import { PersistGate } from "redux-persist/integration/react";
import Loader from "./components/Loader";
import { Provider } from "react-redux";
import { persistor, store } from "./redux/store";
import ScrollToTop from "./components/ScrollToTop";
import { PayPalScriptProvider } from "@paypal/react-paypal-js";
import AppRoutes from "./utils/routes/AppRoutes";

const queryClient = new QueryClient();

const firebaseConfig = {
  apiKey: "AIzaSyDVB_uUQoTr6Go25CuAaTO05nkMd6owdm4",
  authDomain: "donee-auth.firebaseapp.com",
  projectId: "donee-auth",
  storageBucket: "donee-auth.appspot.com",
  messagingSenderId: "77416628680",
  appId: "1:77416628680:web:d56f294dd6052d913900fd",
  measurementId: "G-TH1FQHHHD9",
};

const App = () => {
  // Initialize Firebase
  const app = initializeApp(firebaseConfig);

  return (
    <Suspense fallback={<Loader />}>
      <ThemeConfig>
        <Provider store={store}>
          <PersistGate loading={null} persistor={persistor}>
            <PayPalScriptProvider
              options={{
                "client-id":
                  "Abtgw5xIRVEkDax83r2dxnVn9ZUKXd4gMBXgkYOsukjhLBlJxv4ZCdHlg57hB54Bsq4uP1UDZfrxZOqZ",
              }}
            >
              <QueryClientProvider client={queryClient}>
                <BrowserRouter>
                  <ScrollToTop />
                  <AppRoutes />
                </BrowserRouter>
              </QueryClientProvider>
            </PayPalScriptProvider>
          </PersistGate>
        </Provider>
      </ThemeConfig>
    </Suspense>
  );
};

export default App;
