import React, { useState, useMemo, useEffect, MouseEvent } from "react";

import { useTransition } from "@react-spring/web";
import { Container, Message, Button, Content, Life } from "./stylesError";
import { Item, MessageHubProps } from "../utils/typeHelpers";
import { Typography } from "@material-ui/core";

let id = 0;

export function MessageError({
  config = { tension: 125, friction: 20, precision: 0.1 },
  timeout = 15000,
  children,
}: MessageHubProps) {
  const refMap = useMemo(() => new WeakMap(), []);
  const cancelMap = useMemo(() => new WeakMap(), []);
  const [items, setItems] = useState<Item[]>([]);

  const transitions = useTransition(items, {
    from: { opacity: 0, height: 0, life: "100%" },
    keys: (item) => item.key,
    enter: (item) => async (next, cancel) => {
      cancelMap.set(item, cancel);
      await next({ opacity: 1, height: refMap.get(item).offsetHeight });
      await next({ life: "0%" });
    },
    leave: [{ opacity: 0 }, { height: 0 }],
    onRest: (result, ctrl, item) => {
      setItems((state) =>
        state.filter((i) => {
          return i.key !== item.key;
        })
      );
    },
    config: (item, index, phase) => (key) =>
      phase === "enter" && key === "life" ? { duration: timeout } : config,
  });

  useEffect(() => {
    children((msg: string) => {
      setItems((state) => [...state, { key: id++, msg }]);
    });
  }, []);

  return (
    <Container>
      {transitions(({ life, ...style }, item) => {
        return (
          <Message style={style}>
            <Content
              ref={(ref: HTMLDivElement) => ref && refMap.set(item, ref)}
            >
              <Life style={{ right: life as any }} />
              <Typography variant="h6">{item.msg}</Typography>
              <Button
                onClick={(e: MouseEvent) => {
                  e.stopPropagation();
                  if (cancelMap.has(item) && life.get() !== "0%")
                    cancelMap.get(item)();
                }}
              >
                X
              </Button>
            </Content>
          </Message>
        );
      })}
    </Container>
  );
}
