// material
import React, { useState, useRef } from "react";
import { styled } from "@material-ui/core/styles";
import {
  Button,
  Card,
  Modal,
  Stack,
  TextField,
  Typography,
} from "@material-ui/core";
import CardImg from "../../../../../../assets/cards/cardpaypal.png";
import EditIcon from "../../../../../../assets/icons/edit.png";
import { customShadows } from "../../../../../../theme/shadows";
import { CardInfoType, CashoutAccountInfo, CashoutBankCardListInfo, RootAppState, AddFunction } from "../../../../../../utils/typeHelpers";
import { Box } from "@material-ui/system";

import { useMutation, UseMutationResult, UseQueryResult, useQuery } from "react-query";
import { CASHOUT_ACCOUNT_INFO, CASHOUT_ACCOUNT_INFO_UPDATE } from "../../../../../../utils/Api";
import { useSelector } from "react-redux";

import { MessageError } from "../../../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../../../components/AlertMessageSuccess";
import useAxios from "../../../../../../utils/useAxios";
import Scrollbar from "../../../../../../components/Scrollbar";
import { isTemplateElement } from "@babel/types";
import Loader from "../../../../../../components/Loader";
import ApiErrorPage from "../../../../../Errror/ApiErrorPage";
import ErrorCatchComponent from "../../../../../../components/ErrorCatchComponent";

const Label = styled(Typography)(() => ({
  fontSize: 24,
  height: 32,
  fontWeight: 600,
  color: "#212529",
}));

const Error = styled(Typography)(() => ({
  fontSize: 16,
  height: 23,
  color: "red",
}));

const CardName = styled(Typography)(() => ({
  fontSize: 14,
  height: 20,
  fontWeight: 500,
  color: "#212529",
}));

// ----------------------------------------------------------------------

export default function AddCard() {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const [open, setOpen] = useState(false);
  const [addCard, setAddCard] = useState(false);

  // console.log('userState.userInfo:',userState.userInfo.user_profile?.id);
 
  let axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<CashoutBankCardListInfo[], Error> = useQuery(
    ["cashoutBankCardListInfo", userState.userInfo.access, addCard],
    async () => {
      const response = await axiosInstance.get(`/cashout-account-list?profile=${userState.userInfo.user_profile?.id}`);

      return response.data;
    }
  );
  // console.log("### bankCardList: " , data);



  const [cardNameError, setCardNameError] = useState(false);
  const [cardNoError, setCardNoError] = useState(false);
  const [cardIdNo, setCardIdNo] = useState(0);

  const [cardInfo, setCardInfo] = useState<CardInfoType>({
    cardName: "",
    cardNo: "",
    type: "PAYPAL",
    errorcardName: "",
    errorcardNo: "",
    action: "INSERT",
  });
  const handleOpen = () => {
    setCardInfo({
      ...cardInfo,
      cardName: '',
      cardNo: '',
    });
    setOpen(true);
  }

  const handleOpenForUpdate = (item:CashoutBankCardListInfo) => {
    console.log('CashoutBankCardListInfo: ',item);
    setCardInfo({
      ...cardInfo,
      action: "UPDATE",
      cardName: item.name,
      cardNo: item.account_number,
    });
    setCardIdNo(item.id);
    setOpen(true);
  }

  const handleClose = () => setOpen(false);

  const handleCardAdd = () => {
    
    if (cardInfo.cardName.trim() === "") {
      setCardInfo({
        ...cardInfo,
        errorcardName: "Card Name can't not be empty.",
      });
      setCardNameError(true);
      setCardNoError(false);
      return;
    }

    if (cardInfo.cardNo.trim() === "") {
      setCardInfo({
        ...cardInfo,
        errorcardNo: "Card Number can't not be empty.",
      });
      setCardNameError(false);
      setCardNoError(true);
      return;
    }

    setCardNameError(false);
    setCardNoError(false);

    if (userState.userInfo.user_profile) {
      const profile = userState.userInfo.user_profile.id;
      const data = {
        name: cardInfo.cardName,
        account_number: cardInfo.cardNo,
        type: cardInfo.type,
        profile: profile
      };
      console.log('comming', data);

      if (cardInfo.action === "INSERT") {
        mutationCreate.mutateAsync(data);
      } else {
        mutationUpdate.mutateAsync(data);
      }
      handleClose();
    } else {
      return;
    } 

  };

  const refSucess = useRef<null | AddFunction>(null);
  const refError = useRef<null | AddFunction>(null);
  const handleCreateError = (msg:any) => {
    refError.current?.(msg);
  };
  const handleCreateSuccess = (msg:any) => {
    refSucess.current?.(msg);
  };

  const mutationCreate = useMutation(async (data:CashoutAccountInfo) => {
    const response = await CASHOUT_ACCOUNT_INFO(
      data,
      userState.userInfo.access
    );
    // console.log(response.message);
    if (response === "404") {
      handleCreateError("Network response was not ok.");
    } else {
      console.log(response);
      handleCreateSuccess("New Card Has Added!");
      setAddCard(!addCard);
      setCardInfo({
        ...cardInfo,
        action: "INSERT",
        cardName: '',
        cardNo: '',
      });
      return response;
    }
  });

  const mutationUpdate = useMutation(async (data:CashoutAccountInfo) => {
    const response = await CASHOUT_ACCOUNT_INFO_UPDATE(
      data,
      userState.userInfo.access,
      cardIdNo
    );
    // console.log(response.message);
    if (response === "404") {
      handleCreateError("Network response was not ok.");
    } else {
      console.log(response);
      handleCreateSuccess("Card Info Has Updated!");
      setAddCard(!addCard);
      setCardInfo({
        ...cardInfo,
        action: "INSERT",
        cardName: '',
        cardNo: '',
      });
      return response;
    }
  });

  return (
      
    
          <Stack
            direction="column"
            justifyContent="space-between"
            alignItems="flex-start"
            spacing={2}
          >
            <MessageError
                  children={(add: AddFunction) => {
                    refError.current = add;
                  }}
                  />
            <MessageSuccess
              children={(add: AddFunction) => {
                refSucess.current = add;
              }}
            />
      
            <Modal
              keepMounted
              open={open}
              onClose={handleClose}
              aria-labelledby="keep-mounted-modal-title"
              aria-describedby="keep-mounted-modal-description"
            >
              <Box
                sx={{
                  position: "absolute" as "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  width: 400,
                  bgcolor: "background.paper",
                  boxShadow: 24,
                  p: 4,
                }}
              >
                { cardNameError && (<Typography style={{color: 'red'}} variant="subtitle1" gutterBottom component="div">
                  {cardInfo.errorcardName}
                </Typography>)}
                <TextField
                  value={cardInfo.cardName}
                  onChange={(e) =>
                    setCardInfo({ ...cardInfo, cardName: e.target.value })
                  }
                  id={`outlined-full-width card`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  variant="outlined"
                  placeholder="Card Name"
                  type="text"
                  label="Card Name"
                  helperText={() => <Error>{cardInfo.errorcardName}</Error>}
                />
      
                { cardNoError && (<Typography style={{color: 'red'}} variant="subtitle1" gutterBottom component="div">
                  {cardInfo.errorcardNo}
                </Typography>)}
                <TextField
                  value={cardInfo.cardNo}
                  onChange={(e) =>
                    setCardInfo({ ...cardInfo, cardNo: e.target.value })
                  }
                  id={`outlined-full-width card`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  variant="outlined"
                  placeholder="Card Number"
                  type="number"
                  label="Card Number"
                  helperText={() => <Error>{cardInfo.errorcardNo}</Error>}
                />
                <Button onClick={handleCardAdd} variant="contained" color="secondary">
                  Save
                </Button>
              </Box>
            </Modal>
            <Stack
              direction="row"
              justifyContent="center"
              alignItems="center"
              spacing={8}
            >
              <Label>Banks and cards</Label>
              <Button onClick={handleOpen} variant="text" color="secondary">
                Add Card
              </Button>
              
            </Stack>

      
            {/* ==========================* Band and Card List *======================== */}
            
              <Box>
                { !data  &&
                <Typography variant="caption" display="block" gutterBottom>
                    No Payment Information is Available!
                </Typography>
                }
              </Box>
            

            {
              data && data.map((item:CashoutBankCardListInfo, index) => (
                <Stack
                key={index}
                direction="row"
                justifyContent="center"
                alignItems="center"
                spacing={8}
                >
                  <img src={CardImg} style={{ height: 52, width: 84 }} />
          
                  <Stack
                    direction="column"
                    justifyContent="flex-start"
                    alignItems="flex-start"
                    spacing={1}
                  >
                    <CardName>{item.name}</CardName>
                    <CardName>{item.account_number}</CardName>
                  </Stack>
                  <img
                    src={EditIcon}
                    style={{ height: 20, width: 20 }}
                    onClick={() => {handleOpenForUpdate(item)}}
                  />
                </Stack>
              ))
            }

            

            
      
          </Stack>
 
  );
}
