import React, { useEffect } from "react";
import jwt_decode from "jwt-decode";
import { lazy } from "@loadable/component";
import { useDispatch, useSelector } from "react-redux";
import { useRoutes, Navigate, useLocation } from "react-router-dom";

//
import { ILoginResponse, RootAppState } from "../typeHelpers";
import { tokenInitialValue } from "../initialvalues";
import { saveLoginAction } from "../../redux/actions/loginAction";
import { saveUserInfoAction } from "../../redux/actions/saveUserInfoAction";
import FundTransfer from "../../screen/Home/NGO/Dashboard/Pages/FundTransfer";
import Faq from "../../screen/Home/Common/Faq/Faq";
//
const CreateProfileDonor = lazy(
  () => import("../../screen/Authentication/Donor/CreateProfileDonor")
);
const ProfileDonorOther = lazy(
  () => import("../../screen/Home/Donor/DonorProfile/DonorProfileOther")
);

const CreateProfileNgo = lazy(
  () => import("../../screen/Authentication/Ngo/CreateProfile")
);
const CreateProfileDonee = lazy(
  () => import("../../screen/Authentication/Donee/CreateProfile")
);
const RegsiterUser = lazy(
  () => import("../../screen/Authentication/RegsiterUser")
);

const LandingPage = lazy(
  () => /* webpackPrefetch: true */ import("../../screen/Home/Common/Landing")
);
const FAQ = lazy(
  () => /* webpackPrefetch: true */ import("../../screen/Home/Common/Faq/Faq")
);
const ViewGoal = lazy(() => import("../../screen/Home/Common/GoalView"));
const ProfileDonor = lazy(() => import("../../screen/Home/Donor/DonorProfile"));

const CreateGoal = lazy(() => import("../../screen/Home/Common/CreateGoal"));
const EditGoal = lazy(() => import("../../screen/Home/Common/EditGoal"));
const Donate = lazy(() => import("../../screen/Home/Common/Donation/index"));

const VerifyDoneeInvitation = lazy(
  () => import("../../screen/Authentication/VerifyDoneeInvitation")
);
const ProfileDetail = lazy(() => import("../../screen/Home/Common/Profile"));
const ProfileDetailOther = lazy(
  () => import("../../screen/Home/Common/Profile/ProfileOther")
);
const SingleCategory = lazy(
  () => import("../../screen/Home/Common/SingleCategory")
);
const Popup = lazy(() => import("../../screen/Home/Test/Popup"));

const Search = lazy(() => import("../../screen/Home/Common/Search"));

const Notifications = lazy(
  () => import("../../screen/Home/Common/Notifications/Notifications")
);

const InviteDonee = lazy(
  () => import("../../screen/Home/NGO/Dashboard/Pages/InviteDone")
);

const DoneeInvitationCode = lazy(
  () => import("../../screen/Authentication/Components/DoneeInvitationCode")
);

const Page404 = lazy(() => import("../../screen/Errror/Page404"));

const AppLayoutwithNavbar = lazy(
  () => import(/* webpackPrefetch: true */ "../../layout/LoggedInLayout")
);
const DashboardLayout = lazy(
  () => import(/* webpackPrefetch: true */ "../../layout/DashboardLayout")
);
const DashboardApp = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/DashboardApp"
    )
);

const Wallet = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/Wallet"
    )
);
const Donee = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/Donee"
    )
);

const Donors = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/Donors"
    )
);
const Goals = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/Goals"
    )
);

const Roles = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/Roles"
    )
);
const AdminWaiting = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/AdminWaiting"
    )
);

const NgoWaiting = lazy(
  () =>
    import(
      /* webpackPrefetch: true */ "../../screen/Home/NGO/Dashboard/Pages/NgoWaiting"
    )
);

function AppRoutes() {
  const location = useLocation();
  const dispatch = useDispatch();
  const state = useSelector((state: RootAppState) => state.AuthReducer);
  const isProfileCreated = state.userInfo.user_profile !== null ? true : false;
  const isUserNgo = () => {
    if (
      state.userInfo.user_profile !== null &&
      state.userInfo.user_profile.profile_type !== null
    ) {
      return state.userInfo.user_profile.profile_type !== null
        ? state.userInfo.user_profile.profile_type === "NGO"
          ? true
          : false
        : false;
    }
  };

  const userInfoInitial: ILoginResponse = tokenInitialValue;
  console.log(location);

  useEffect(() => {
    let isExpired = false;
    if (state.userInfo.access !== "") {
      const user: any = jwt_decode(state.userInfo.access);
      const unixNow = Math.round(new Date().getTime() / 1000);
      isExpired = unixNow > user.exp;
    }
    if (isExpired) {
      dispatch(saveLoginAction(false));
      dispatch(saveUserInfoAction(userInfoInitial));
    }
  }, [state]);

  return useRoutes([
    //withnavbar
    {
      path: "/",
      element: <AppLayoutwithNavbar />,
      children: [
        {
          path: "",
          element: <LandingPage />,
        },
        {
          path: "faq",
          element: <Faq />,
        },
        {
          path: "singlecategory",
          element: <SingleCategory />,
        },
        {
          path: "search",
          element: <Search />,
        },
        {
          path: "notifications",
          element: <Notifications />,
        },
        {
          path: "profiledonorother",
          element: <ProfileDonorOther />,
        },
        {
          path: "verifydoneeinvite/:id",
          element: !isProfileCreated ? (
            <VerifyDoneeInvitation />
          ) : (
            <Navigate to="../" replace />
          ),
        },
        {
          path: "doneeinvitationcode",
          element: state.isLoggedIn ? (
            <DoneeInvitationCode />
          ) : (
            <Navigate to="../login" replace />
          ),
        },
        {
          path: "donate",
          element: state.isLoggedIn ? (
            <Donate />
          ) : (
            <Navigate to="../login" replace />
          ),
        },
        {
          path: "popup",
          element: state.isLoggedIn ? (
            <Popup />
          ) : (
            <Navigate to="../login" replace />
          ),
        },
        {
          path: "profile",
          element: state.isLoggedIn ? (
            <ProfileDonor />
          ) : (
            <Navigate to="../login" replace />
          ),
        },
        {
          path: "profiledonorother",
          element: state.isLoggedIn ? (
            <ProfileDonorOther />
          ) : (
            <Navigate to="../login" replace />
          ),
        },
        {
          path: "createprofiledonor",
          element:
            state.isLoggedIn && !state.userInfo.is_account_created ? (
              <CreateProfileDonor />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "createprofiledonee",
          element:
            state.isLoggedIn && !isProfileCreated ? (
              <CreateProfileDonee />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "createprofilengo",
          element:
            state.isLoggedIn && !isProfileCreated ? (
              <CreateProfileNgo />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "creategoal",
          element:
            state.isLoggedIn && isProfileCreated ? (
              <CreateGoal />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "editgoal",
          element:
            state.isLoggedIn && isProfileCreated ? (
              <EditGoal />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "profiledetail",
          element:
            state.isLoggedIn && isProfileCreated ? (
              <ProfileDetail />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        {
          path: "profiledetailother",
          element: <ProfileDetailOther />,
        },
        {
          path: "invitedonee",
          element:
            state.isLoggedIn && isProfileCreated && isUserNgo ? (
              <InviteDonee />
            ) : (
              <Navigate to="../" replace />
            ),
        },
        { path: "*", element: <Page404 /> },
      ],
    },

    //withoutnavbar

    {
      path: "viewgoal/:slug",
      element: <ViewGoal />,
    },
    {
      path: "login",
      element: <RegsiterUser />,
    },
    {
      path: "verifyuser/:id",
      element: !isProfileCreated ? (
        <RegsiterUser />
      ) : (
        <Navigate to="../" replace />
      ),
    },

    //dashbordngo
    {
      path: "/dashboard",
      element:
        state.isLoggedIn && isProfileCreated && isUserNgo ? (
          <div>
            <DashboardLayout />
          </div>
        ) : (
          <Navigate to="../" replace />
        ),
      children: [
        {
          path: "/dashboard",
          element: <Navigate to="/dashboard/app" replace />,
        },
        { path: "app", element: <DashboardApp /> },
        { path: "wallet", element: <Wallet /> },
        { path: "goals", element: <Goals /> },
        { path: "donee", element: <Donee /> },
        { path: "donors", element: <Donors /> },
        { path: "roles", element: <Roles /> },
        { path: "notifications", element: <Notifications /> },
        { path: "fundtransfer", element: <FundTransfer /> },
        { path: "waitingadmin", element: <AdminWaiting /> },
        { path: "waitingngo", element: <NgoWaiting /> },
      ],
    },
  ]);
}
export default AppRoutes;
