import React, { useState } from "react";
import { ErrorBoundary } from "react-error-boundary";
import PageErrorBoundary from "../screen/Errror/PageErrorBoundary";

interface ErrorCatchComponentProps {
  children: JSX.Element;
}

const ErrorCatchComponent = ({ children }: ErrorCatchComponentProps) => {
  const [errorExplode, setErrrorExplode] = useState(false);
  return (
    <ErrorBoundary
      fallbackRender={({ error, resetErrorBoundary }) => (
        <PageErrorBoundary
          error={error}
          resetErrorBoundary={resetErrorBoundary}
        />
      )}
      onReset={() => setErrrorExplode(false)}
      resetKeys={[errorExplode]}
    >
      {children}
    </ErrorBoundary>
  );
};

export default ErrorCatchComponent;
