const TABLE_HEAD = [
  { id: "selectBox", label: "", alignRight: false },
  { id: "goals", label: "Goals", alignRight: false },
  { id: "progress", label: "Progress", alignRight: false },
  { id: "owner", label: "Owner", alignRight: false },
  // { id: "enddate", label: "End date", alignRight: false },
  { id: "status", label: "Status", alignRight: false },
  { id: "cashout", label: "Cashout", alignRight: false },
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

export function applySortFilter(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(
      array,
      (_user) => _user.title.toLowerCase().indexOf(query.toLowerCase()) !== -1
    );
  }
  return stabilizedThis.map((el) => el[0]);
}

export function applySortFilterhistory(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_user) => {
      if (_user.goal) {
        return (
          _user.goal.title.toLowerCase().indexOf(query.toLowerCase()) !== -1
        );
      }
    });
  }
  return stabilizedThis.map((el) => el[0]);
}

export default function Goals() {
  // const [goalsList, setGoalsList] = useState([]);
  // const [goalsCount, setgoalsCount] = useState([]);

  const userState = useSelector((state) => state.AuthReducer);
  const [goalStatus, setGoalStatus] = useState(false);

  // -------------------------------------
  const axiosInstance = useAxios();
  const {
    data: goalsList,
    isLoading,
    isError,
    error,
  } = useQuery(
    ["goalsList", userState.userInfo.access, goalStatus],
    async () => {
      const response = await axiosInstance.get(`/dashboard/goal-list/`);
      return response.data;
    }
  );
  // console.log("### goalsList: " , goalsList);

  const {
    data: goalsCount,
    isLoading: isLoadingGoalCount,
    isError: isErrorGoalCount,
    error: errorGoalCount,
  } = useQuery(
    ["goalsCount", userState.userInfo.access, goalStatus],
    async () => {
      const response = await axiosInstance.get(`/dashboard/goal-count/`);
      return response.data;
    }
  );
  // console.log(":::goalsCount###: " , goalsCount);
  // -------------------------------------------

  // const loadDashboardGoalsList = () => {
  //   GET_DASHBOARD_GOALS_LIST_API(userState.userInfo.access).then((data) => {
  //     if (data) {
  //       setGoalsList(data);
  //     }
  //   });
  // };

  // const loadDashboardGoalsCount = () => {
  //   GET_DASHBOARD_GOALS_COUNT_API(userState.userInfo.access).then((data) => {
  //     if (data) {
  //       setgoalsCount(data);
  //     }
  //   });
  // };

  // useEffect(() => {
  //   loadDashboardGoalsList();
  //   loadDashboardGoalsCount();
  // }, [goalStatus]);

  const [page, setPage] = useState(0);
  const [order, setOrder] = useState("asc");
  const [selected, setSelected] = useState([]);
  const [filterName, setFilterName] = useState("");
  const [orderBy, setOrderBy] = useState("title");

  const [rowsPerPage, setRowsPerPage] = useState(5);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = goalsList.map((n) => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - goalsList?.length) : 0;

  const filteredUsers = applySortFilter(
    goalsList ? goalsList : [],
    getComparator(order, orderBy),
    filterName
  );

  const isUserNotFound = filteredUsers.length === 0;

  // =========================================================

  const refError = useRef(null);
  const handleCreateError = (msg) => {
    refError.current?.(msg);
  };

  const mutationCreate = useMutation(async (dataReq) => {
    const response = await UPDATE_GOAL_STATUS_API(
      dataReq,
      userState.userInfo.access
    );

    if (response === "404") {
      handleCreateError("Network response was not ok.");
    } else {
      setGoalStatus(!goalStatus);

      return response;
    }
  });

  const SORT_BY_OPTIONS = [
    { value: "PENDING", label: "Pending" },
    { value: "ACTIVE", label: "Active" },
    { value: "REJECTED", label: "Reject" },
    { value: "COMPLETED", label: "Complete" },
  ];
  const [selectedIndex, setSelectedIndex] = useState(0);
  console.log(selected);
  return (
    <Container maxWidth="xl">
      <MessageError
        children={(add) => {
          refError.current = add;
        }}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mb={5}
      >
        <Typography
          sx={{
            fontSize: 32,
            fontWeight: "bold",
            height: 40,
            color: "#212529",
          }}
        >
          Goals
        </Typography>
        <CustomButton
          variant="contained"
          component={RouterLink}
          to="../../creategoal"
          startIcon={<Icon icon={plusFill} />}
        >
          Create New Goal
        </CustomButton>
      </Stack>

      <Grid container spacing={3} sx={{ pb: 5 }}>
        <Grid item xs={12} sm={12} md={3}>
          <AverageDone
            average_goal_conversion_rate={
              goalsCount?.goal_count
                ? Number.parseFloat(
                    goalsCount.goal_count.average_goal_conversion_rate
                  ).toFixed(2)
                : 0
            }
            // average_goal_conversion_rate={20}
          />
        </Grid>

        <Grid item xs={12} sm={12} md={9}>
          <CardGoals
            active_goals={
              goalsCount?.goal_count ? goalsCount.goal_count.active_goals : 0
            }
            completed_goals={
              goalsCount?.goal_count ? goalsCount.goal_count.completed_goals : 0
            }
            pending_goals={
              goalsCount?.goal_count ? goalsCount.goal_count.pending_goals : 0
            }
            rejected_goals={
              goalsCount?.goal_count ? goalsCount.goal_count.rejected_goals : 0
            }
          />
        </Grid>
      </Grid>

      <Card sx={{ boxShadow: customShadows.z1 }}>
        <UserListToolbar
          numSelected={selected.length}
          filterName={filterName}
          onFilterName={handleFilterByName}
        />

        <Scrollbar>
          <TableContainer sx={{ minWidth: 800 }}>
            <Table>
              <UserListHead
                order={order}
                orderBy={orderBy}
                headLabel={TABLE_HEAD}
                rowCount={goalsList?.length}
                numSelected={selected.length}
                onRequestSort={handleRequestSort}
                onSelectAllClick={handleSelectAllClick}
              />
              <TableBody>
                {filteredUsers
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row) => {
                    const {
                      id,
                      title,
                      status,
                      paid_amount,
                      total_amount,
                      profile,
                    } = row;
                    const isItemSelected = selected.indexOf(id) !== -1;

                    const handleStatusChange = async (index) => {
                      setSelectedIndex(index);
                      const data = {
                        goal: id,
                        status: SORT_BY_OPTIONS[index].value,
                      };
                      mutationCreate.mutateAsync(data);
                    };
                    // console.log(id);
                    return (
                      <TableRow
                        sx={{
                          backgroundColor: "#EEF8FF",
                        }}
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell padding="checkbox">
                          <Checkbox
                            checked={isItemSelected}
                            onChange={(event) => handleClick(event, id)}
                          />
                        </TableCell>
                        <GoalName align="left">{title}</GoalName>
                        <TableCell align="left">
                          <Stack>
                            <ProgressText>
                              ${paid_amount}{" "}
                              <SpanText>of ${total_amount}</SpanText>
                            </ProgressText>
                            <Box sx={{ display: "flex", alignItems: "center" }}>
                              <Box sx={{ width: "100%", mr: 1 }}>
                                <LinearProgress
                                  variant="determinate"
                                  value={parseInt(
                                    (paid_amount / total_amount) * 100
                                  )}
                                />
                              </Box>
                              <Box sx={{ minWidth: 35 }}>
                                <Typography
                                  variant="body2"
                                  color="text.secondary"
                                >
                                  {parseInt((paid_amount / total_amount) * 100)}
                                  %
                                </Typography>
                              </Box>
                            </Box>
                          </Stack>
                        </TableCell>
                        <GoalOwner align="left">
                          {profile.profile_username}
                        </GoalOwner>

                        <TableCell align="left">
                          <Stack
                            direction="row"
                            alignItems="center"
                            spacing={2}
                            justifyContent="space-between"
                          >
                            <Label
                              variant="ghost"
                              color={status === "active" ? "success" : "error"}
                            >
                              {sentenceCase(status)}
                            </Label>
                            <DropDownMenu
                              selectedIndex={selectedIndex}
                              handleStatusChange={handleStatusChange}
                              options={SORT_BY_OPTIONS}
                            />
                          </Stack>
                        </TableCell>
                        <PostsNo align="left">
                          <PopupModal />
                        </PostsNo>
                      </TableRow>
                    );
                  })}
                {emptyRows > 0 && (
                  <TableRow style={{ height: 53 * emptyRows }}>
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>
              {isUserNotFound && (
                <TableBody>
                  <TableRow>
                    <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                      <SearchNotFound searchQuery={filterName} />
                    </TableCell>
                  </TableRow>
                </TableBody>
              )}
            </Table>
          </TableContainer>
        </Scrollbar>

        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={goalsList?.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Card>
    </Container>
  );
}
