// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
// utils

import DonateIcon from "../../../../../../assets/icons/donate.svg";
import { customShadows } from "../../../../../../theme/shadows";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z24,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: "lighter",
  color: "#6C757D",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "black",
}));

// ----------------------------------------------------------------------

const TOTAL = 135;

export default function TotalDonations( { total_donations }: { total_donations: any } ) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1}
      >
        <img src={DonateIcon} alt="s" style={{ height: 30, width: 30 }} />
        <Label>Donations</Label>
        <Value>{total_donations}</Value>
      </Stack>
    </RootStyle>
  );
}
