import { styled } from "@material-ui/styles";
import { animated } from "@react-spring/web";

const Main = styled("div")({
  cursor: "pointer",
  color: "#676767",
  WebkitUserSelect: "none",
  userSelect: "none",
  display: "flex",
  alignItems: "center",
  height: "100%",
  width: 250,
  justifyContent: "center",
});

const Container = styled("div")({
  position: "fixed",
  zIndex: 1000,
  width: "0 auto",
  top: 30,
  margin: "0 auto",
  left: 5,
  right: 5,
  display: "flex",
  flexDirection: "column",
  pointerEvents: "none",
  alignItems: "center",
});

const Message = styled(animated.div)({
  position: "relative",
  overflow: "hidden",
  width: "40ch",
  boxSizing: "border-box",
});

const Content = styled("div")({
  color: "white",
  backgroundImage: "linear-gradient(130deg, #11998e, #38ef7d)",
  opacity: 0.9,
  padding: 22,
  fontSize: "1em",
  display: "grid",
  gridTemplateColumns: "1fr auto",
  gridGap: 10,
  overflow: "hidden",
  height: "auto",
  borderRadius: 3,
  marginTop: 10,
});

const Button = styled("div")({
  cursor: "pointer",
  pointerEvents: "all",
  outline: 0,
  border: "none",
  backgroundColor: "transparent",
  display: "flex",
  alignSelf: "flex-end",
  overflow: "hidden",
  margin: 0,
  padding: 0,
  paddingBlock: 14,
  color: "rgba(255, 255, 255, 0.5)",
  "&:hover": {
    color: "rgba(255, 255, 255, 0.6)",
  },
});

const Life = styled(animated.div)({
  position: "absolute",
  bottom: 0,
  left: 0,
  width: "auto",
  backgroundImage: "linear-gradient(130deg, #ed213a, #f26375)",

  height: 5,
});

export { Life, Button, Content, Message, Main, Container };
