import React, { useEffect, useState, useRef } from "react";
import { styled } from "@material-ui/styles";
import { Box } from "@material-ui/system";
import {
  FormControl,
  InputLabel,
  Modal,
  Select,
  SelectChangeEvent,
  Stack,
  Typography,
  MenuItem,
  TextField,
  CircularProgress,
  Button,
} from "@material-ui/core";
import { useMutation, useQuery, UseQueryResult } from "react-query";

import useAxios from "../../../../../../utils/useAxios";
import ApiErrorPage from "../../../../../Errror/ApiErrorPage";
import { AddFunction } from "../../../../../../utils/typeHelpers";

import { MessageSuccess } from "../../../../../../components/AlertMessageSuccess";
import { MessageError } from "../../../../../../components/AlertMessageError";

const RootStyle = styled(Stack)(() => ({
  top: "50%",
  left: "50%",
  position: "absolute",
  width: 400,
  backgroundColor: "white",
  padding: 20,
  transform: "translate(-50%, -50%)",
}));

interface UserProps {
  id: number;
  email: string;
}

interface RoleProps {
  id: number;
  role_type: string;
}

interface RoleCreateProps {
  role: number | null;
  user: number | null;
}

const AddNewMember = ({
  open,
  handleClose,
}: {
  open: boolean;
  handleClose: () => void;
}) => {
  const axiosInstance = useAxios();

  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const [keyword, setKeyword] = useState<string>("");
  const [selectedUser, setselectedUser] = useState("");
  const [assignRole, setAssignRole] = useState("");
  const [newUser, setNewUser] = useState<RoleCreateProps>({
    role: null,
    user: null,
  });

  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<UserProps[], Error> = useQuery(
    ["roleUserListDashboard", keyword],
    async () => {
      const response = await axiosInstance.get(
        `/search-user/?query=${keyword}`
      );

      return response.data;
    }
  );

  const rolesQuery: UseQueryResult<RoleProps[], Error> = useQuery(
    ["roleListDashboard"],
    async () => {
      const response = await axiosInstance.get(`/role-list/`);

      return response.data;
    }
  );

  const mutationRoleAssign = useMutation(async (dataReq: RoleCreateProps) => {
    const response = await axiosInstance.post(`ngo-user-create/`, {
      role: dataReq.role,
      user: dataReq.user,
    });
    return response;
  });

  const handleChange = (event: SelectChangeEvent) => {
    if (event.target.value) {
      setselectedUser(event.target.value as string);
      setNewUser({ ...newUser, user: parseInt(event.target.value) });
    }
  };

  const handleAssignRole = () => {
    mutationRoleAssign.mutateAsync(newUser);
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  const { isSuccess, isError: mutationisError } = mutationRoleAssign;
  useEffect(() => {
    if (isSuccess) {
      handleCreateSucess("Role Assigned");
      setTimeout(() => {
        handleClose();
      }, 2000);
    } else if (mutationisError) {
      handleCreateError("Role Assigne Falied");
    }
  }, [isSuccess, mutationisError]);
  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <RootStyle
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={2}
      >
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />
        <MessageSuccess
          children={(add: AddFunction) => {
            refSuccess.current = add;
          }}
        />
        <Typography id="modal-modal-title" variant="h6" component="h2">
          Add new member in your team
        </Typography>
        <TextField
          fullWidth
          id="filled-basic"
          label="Find user by username or email"
          variant="outlined"
          value={keyword}
          onChange={(e) => setKeyword(e.target.value)}
        />

        {!isLoading ? (
          isError ? (
            <ApiErrorPage error={Error(error?.message)} />
          ) : (
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Select User</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={selectedUser}
                label="Select User"
                onChange={handleChange}
              >
                {data &&
                  data.map((item) => (
                    <MenuItem key={`${item.id}`} value={item.id}>
                      {item.email}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          )
        ) : (
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        )}
        {!rolesQuery.isLoading ? (
          rolesQuery.isError ? (
            <ApiErrorPage error={Error("Network Error")} />
          ) : (
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Select Role</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={assignRole}
                label="Select Role"
                onChange={(e) => {
                  setAssignRole(e.target.value);
                  setNewUser({ ...newUser, role: parseInt(e.target.value) });
                }}
              >
                {rolesQuery.data &&
                  rolesQuery.data.map((item) => (
                    <MenuItem key={`${item.id}`} value={item.id}>
                      {item.role_type}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          )
        ) : (
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        )}
        <Button onClick={handleAssignRole} variant="outlined">
          Assign Role
        </Button>
      </RootStyle>
    </Modal>
  );
};

export default AddNewMember;
