import React from "react";
import {
  Avatar,
  Typography,
  CardContent,
  CardHeader,
  Grid,
  Stack,
  CardMedia,
} from "@material-ui/core";
import { styled } from "@material-ui/core/styles";

import AvatarImage1 from "../../../../../../assets/cards/avatarsmall1.png";
import AvatarImage2 from "../../../../../../assets/cards/avatarsmall2.png";
import AvatarImage3 from "../../../../../../assets/cards/avatarsmall3.png";
import AvatarImage4 from "../../../../../../assets/cards/avatarsmall4.png";
import AvatarAdd from "../../../../../../assets/cards/addavatar.png";
import AvatarAddIcon from "../../../../../../assets/icons/add_avatar.svg";
const AVATARS = [
  AvatarImage1,
  AvatarImage2,
  AvatarImage3,
  AvatarImage4,
  AvatarAdd,
];

const RootStyle = styled(Stack)(() => ({
  padding: 20,

  height: 150,
}));

const Label = styled(Typography)(() => ({
  fontSize: 24,
  height: 32,
  fontWeight: "bold",
  color: "#000000",
}));

const TeamMember = () => {
  return (
    <RootStyle
      direction="column"
      justifyContent="center"
      alignItems="flex-start"
      spacing={1}
    >
      <Label>Your Team</Label>
      <CardContent>
        <Grid container spacing={2} justifyContent="center" alignItems="center">
          {AVATARS.map((avatar, index) => {
            return (
              <>
                {index === 4 ? (
                  <CardMedia
                    image={avatar}
                    sx={{
                      width: 56,
                      height: 56,
                      marginRight: 2,
                      justifyContent: "center",
                      alignItems: "center",
                      display: "flex",
                    }}
                  >
                    <Avatar
                      src={AvatarAddIcon}
                      style={{ height: 30, width: 30 }}
                    />
                  </CardMedia>
                ) : (
                  <Avatar
                    alt="Remy Sharp"
                    src={avatar}
                    sx={{ width: 56, height: 56, marginRight: 2 }}
                  />
                )}
              </>
            );
          })}
        </Grid>
      </CardContent>
    </RootStyle>
  );
};

export default TeamMember;
