import { CardHeader, Card } from "@material-ui/core";
import { styled, useTheme } from "@material-ui/styles";
import { merge } from "lodash";
import ReactApexChart from "react-apexcharts";
// material

// ----------------------------------------------------------------------
import BaseOptionChart from "../../../../../../components/BaseOptionChart";
import { fNumber } from "../../../../../../utils/formatNumber";
// ----------------------------------------------------------------------
const CHART_HEIGHT = 372;
const LEGEND_HEIGHT = 72;

const ChartWrapperStyle = styled("div")(({ theme }) => ({
  height: CHART_HEIGHT,
  marginTop: 2,
  "& .apexcharts-canvas svg": { height: CHART_HEIGHT },
  "& .apexcharts-canvas svg,.apexcharts-canvas foreignObject": {
    overflow: "visible",
  },
  "& .apexcharts-legend": {
    height: LEGEND_HEIGHT,
    alignContent: "center",
    position: "relative !important",
    borderTop: `solid 1px ${theme.palette.divider}`,
    top: `calc(${CHART_HEIGHT - LEGEND_HEIGHT}px) !important`,
  },
}));

// ----------------------------------------------------------------------

export default function AppCurrentVisits({ chartData }) {
  const theme = useTheme();
  console.log(chartData);
  const CHART_DATA = chartData[0].map((item) => parseInt(item));
  const chartOptions = merge(BaseOptionChart(), {
    colors: [
      theme.palette.primary.main,
      theme.palette.info.main,
      theme.palette.warning.main,
      theme.palette.error.main,
    ],
    labels: chartData[1],
    stroke: { colors: [theme.palette.background.paper] },
    legend: { floating: true, horizontalAlign: "center" },
    dataLabels: { enabled: true, dropShadow: { enabled: false } },
    tooltip: {
      fillSeriesColor: false,
      y: {
        formatter: (seriesName) => fNumber(seriesName),
        title: {
          formatter: (seriesName) => `#${seriesName}`,
        },
      },
    },
    plotOptions: {
      donut: { donut: { labels: { show: false } } },
    },
  });

  return (
    <Card>
      <CardHeader
        title="Donors by Country"
        sx={{
          color: "#6C757D",
          fontSize: 20,
          fontWeight: 600,
          textAlign: "center",
        }}
      />
      <ChartWrapperStyle dir="ltr">
        <ReactApexChart
          type="donut"
          series={CHART_DATA}
          options={chartOptions}
          height={280}
        />
      </ChartWrapperStyle>
    </Card>
  );
}
