import PropTypes from "prop-types";
import { Icon } from "@iconify/react";

import { formatDistance } from "date-fns";
import { Link as RouterLink } from "react-router-dom";
import arrowIosForwardFill from "@iconify/icons-eva/arrow-ios-forward-fill";
// material
import {
  Box,
  Stack,
  Link,
  Card,
  Button,
  Divider,
  Typography,
  CardHeader,
} from "@material-ui/core";

import { styled } from "@material-ui/core/styles";
// utils

import Scrollbar from "../../../../../../components/Scrollbar";
import DonateIcon from "../../../../../../assets/icons/donatebox.png";
import DownloadIcon from "../../../../../../assets/icons/download.png";
import {
  DonorsApiProps,
  RecentActivityEntity,
} from "../../../../../../utils/typeHelpers";
//

// ----------------------------------------------------------------------
const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: "none",
  backgroundColor: "transparent",
}));
const Title = styled(Typography)(() => ({
  fontSize: 16,
  height: 24,
  fontWeight: 500,
  color: "#000000",
}));

const DateText = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: "normal",
  color: "#CED4DA",
}));

const SendTo = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: "normal",
  color: "#6C757D",
}));
const Value = styled(Typography)(() => ({
  fontSize: 14,
  height: 22,
  fontWeight: 500,
}));

const CustomButton = styled(Button)(() => ({
  width: 91,
  height: 34,
  backgroundColor: "white",
  border: "1px solid #6C757D",
  color: "#6C757D",
  fontSize: 14,
  fontWeight: "normal",
}));

function ActivityItem({ item }: { item: RecentActivityEntity }) {
  const { donor_fullname, created_at, amount, donee_fullname } = item;

  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-between"
      spacing={1}
    >
      <img src={DonateIcon} />
      <Box sx={{ minWidth: 200 }}>
        <Link to="#" color="inherit" underline="hover" component={RouterLink}>
          <Title noWrap>{donor_fullname}</Title>
        </Link>
        <DateText>{created_at}</DateText>
        <SendTo>To: {donee_fullname}</SendTo>
      </Box>
      <Value
        sx={{
          color: "rgba(22, 182, 152, 0.65)",
          // color:
          //   sign === "+"
          //     ? "rgba(22, 182, 152, 0.65)"
          //     : "rgba(207, 16, 16, 0.65)",
        }}
      >
        {/* {sign} */}
        {amount}
      </Value>
      <CustomButton>Preview</CustomButton>
      <img src={DownloadIcon} />
    </Stack>
  );
}

export default function RecentActivities({
  recentActivity,
}: {
  recentActivity: RecentActivityEntity[];
}) {
  return (
    <RootStyle>
      <CardHeader
        title="Recent Activities"
        style={{
          color: "#212529",
          fontSize: 24,
          fontWeight: "bold",
        }}
      />

      <Scrollbar
        sx={{
          height: 310,
          "& .simplebar-content": {
            height: "100%",
            display: "flex",
            flexDirection: "column",
          },
        }}
      >
        {recentActivity !== null && recentActivity !== undefined && (
          <Stack spacing={3} sx={{ p: 3, pr: 0 }}>
            {recentActivity.map((item, i) => (
              <ActivityItem key={`${i}`} item={item} />
            ))}
          </Stack>
        )}
      </Scrollbar>

      {/* <Divider /> */}

      {/* <Box sx={{ p: 2, textAlign: "right" }}>
        <Button
          to="#"
          size="small"
          color="inherit"
          component={RouterLink}
          endIcon={<Icon icon={arrowIosForwardFill} />}
        >
          See More
        </Button>
      </Box> */}
    </RootStyle>
  );
}
