// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
import { Icon } from "@iconify/react";
import ArrowUp from "@iconify/icons-ant-design/arrow-up-outlined";

import { customShadows } from "../../../../../../theme/shadows";

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z24,
  height: 150,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 25,
  fontWeight: 600,
  color: "#16B698",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "black",
}));

const TypographyBody = styled(Typography)(() => ({
  color: "#CED4DA",
  fontsize: 14,
}));
// ----------------------------------------------------------------------

export default function TotalCollected(
  {total_collected, total_today_collected} : 
  {total_collected:any, total_today_collected:any}) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1}
      >
        <Label>Total Collected</Label>

        <Value>${total_collected}</Value>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={0}
        >
          <Icon icon={ArrowUp} width={16} height={16} color="#16B698" />
          <TypographyBody variant="body1">${total_today_collected} Today</TypographyBody>
        </Stack>
      </Stack>
    </RootStyle>
  );
}
