import React, { useState } from "react";
import { styled } from "@material-ui/core/styles";
import {
  Avatar,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Card,
  CardHeader,
  Stack,
  Typography,
} from "@material-ui/core";
import { useSelector } from "react-redux";

import {
  FundsProps,
  FundTransferHistory,
  RootAppState,
} from "../../../../../../utils/typeHelpers";
import { applySortFilterhistory } from "../Goals";
import { customShadows } from "../../../../../../theme/shadows";
import { UserListHead, UserListToolbar } from ".";
import Scrollbar from "../../../../../../components/Scrollbar";

import SearchNotFound from "../../../../../../components/SearchNotFound";

function getComparator(order: string, orderBy: keyof FundsProps) {
  return order === "desc"
    ? (a: FundsProps, b: FundsProps) => descendingComparator(a, b, orderBy)
    : (a: FundsProps, b: FundsProps) => -descendingComparator(a, b, orderBy);
}

function descendingComparator(
  a: FundsProps,
  b: FundsProps,
  orderBy: keyof FundsProps
) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

const TABLE_HEAD = [
  { id: "image", label: "Avatar", alignRight: false },
  { id: "title", label: "Title", alignRight: false },
  { id: "requested_amount", label: "Requested Amount", alignRight: false },
  { id: "remark", label: "Remark", alignRight: false },
  { id: "status", label: "Status", alignRight: false },
];

const FundTransferHistoryTable = ({
  data,
}: {
  data: FundTransferHistory[];
}) => {
  const profile_image = useSelector(
    (state: RootAppState) =>
      state.AuthReducer.userInfo.user_profile?.profile_image
  );

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [filterName, setFilterName] = useState("");
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState<keyof FundsProps>("title");

  const handleFilterByName = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFilterName(event.target.value);
  };

  const handleRequestSort = (
    event: React.ChangeEvent<HTMLInputElement>,
    property: keyof FundsProps
  ) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const filteredUsers = applySortFilterhistory(
    data,
    getComparator(order, orderBy),
    filterName
  );

  const isUserNotFound = filteredUsers.length === 0;

  const handleChangePage = (event: any, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <Card sx={{ boxShadow: customShadows.z1 }}>
      <CardHeader title="Fund Transfer History Table" />

      <UserListToolbar
        filterName={filterName}
        onFilterName={handleFilterByName}
      />

      <Scrollbar sx={{}}>
        <TableContainer sx={{ minWidth: 800 }}>
          <Table>
            <UserListHead
              order={order}
              orderBy={orderBy}
              headLabel={TABLE_HEAD}
              rowCount={data.length}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {filteredUsers
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row: FundTransferHistory) => {
                  const { goal, requested_amount, remark, status } = row;

                  const handleStatusChange = async (index: number) => {};
                  return (
                    <TableRow>
                      <TableCell align="left">
                        {goal === null ? (
                          <Avatar src={profile_image} />
                        ) : (
                          <Avatar src={goal.image} />
                        )}
                      </TableCell>
                      {goal === null ? (
                        <GoalName align="left">Donee Amount</GoalName>
                      ) : (
                        <GoalName align="left">{goal.title}</GoalName>
                      )}

                      <TableCell align="left">${requested_amount}</TableCell>
                      <TableCell align="left">{remark}</TableCell>
                      <TableCell align="left">
                        <Stack
                          direction="row"
                          alignItems="center"
                          spacing={2}
                          justifyContent="space-between"
                        >
                          <Typography
                            sx={{
                              color:
                                status !== "PENDING" ? "#16B698" : "tomato",
                            }}
                          >
                            {status}
                          </Typography>
                        </Stack>
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
            {isUserNotFound && (
              <TableBody>
                <TableRow>
                  <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                    <SearchNotFound searchQuery={filterName} />
                  </TableCell>
                </TableRow>
              </TableBody>
            )}
          </Table>
        </TableContainer>
      </Scrollbar>

      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={data.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Card>
  );
};

const GoalName = styled(TableCell)(() => ({
  color: "#343A40",
  fontsize: 14,
  fontWeight: "normal",
  textTransform: "capitalize",
}));

export default FundTransferHistoryTable;
