import { authReducerStateInitialValue } from "../utils/initialvalues";
import { ActionAuthReducrer, AuthRootStateType } from "../utils/typeHelpers";
import {
  USER_LOGGING_IN,
  USER_LOGGING_OUT,
  SAVE_USER_INFO,
  SAVE_NGO_PROFILEID_ACTION,
  SAVE_NEW_TOKEN_ACTION,
} from "./types";

const AuthReducer = (
  state = authReducerStateInitialValue,

  action: ActionAuthReducrer
): AuthRootStateType => {
  switch (action.type) {
    case USER_LOGGING_IN:
      const paylodLogin = action.payload;
      return {
        ...state,
        isLoggedIn: paylodLogin,
      };
    case SAVE_USER_INFO:
      const paylodUserInfo = action.payload;
      return {
        ...state,
        userInfo: paylodUserInfo,
      };
    case SAVE_NEW_TOKEN_ACTION:
      const paylodNewToken = action.payload;
      console.log(paylodNewToken);

      return {
        ...state,
        userInfo: {
          ...state.userInfo,
          access: paylodNewToken.access,
          refresh: paylodNewToken.refresh,
        },
      };
    case SAVE_NGO_PROFILEID_ACTION:
      const paylodID = action.payload;
      return {
        ...state,
        ngo_profile_id: paylodID,
      };
    case USER_LOGGING_OUT:
      return authReducerStateInitialValue;

    default:
      return state;
  }
};

export { AuthReducer };
