const SIGNUP_API = async (data: SignupType) => {
  try {
    const request = await fetch(`${API_URL}/create-user/`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

const SOCIAL_SIGNUP_API = async (data: SocialSignupType) => {
  try {
    const request = await fetch(`${API_URL}/social-signup/`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const response = await request.json();

    if (request.ok) {
      return response;
    } else {
      if (response.email[0] === "This field must be unique.") {
        return "Email already used";
      }
      return "Network error";
    }
  } catch (error) {
    return error;
  }
};

