const UPDATE_GOAL_STATUS_API = async (
  goalStatus: GoalStatusType,
  token: string
) => {
  try {
    const formdata = new FormData();
    formdata.append("goal", `${goalStatus.goal}`);
    formdata.append("status", `${goalStatus.status}`);

    const request = await fetch(`${API_URL}/goal-status/`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
      redirect: "follow",
    });

    if (request.ok) {
      const response = await request.json();
      return response;
    } else {
      return "404";
    }
  } catch (error) {
    return error;
  }
};

const SEND_INVITATION_LINK = async (
  invitation: InvitationType,
  token: string
) => {
  try {
    const formdata = new FormData();
    invitation.emails.map((email) => formdata.append("emails", `${email}`));
    // formdata.append("emails", `${invitation.emails}`);
    formdata.append("invitation_link", `${invitation.invitation_link}`);

    const request = await fetch(`${API_URL}/send-invitation-link/`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
      redirect: "follow",
    });

    if (request.ok) {
      const response = await request.json();
      return response;
    } else {
      return "404";
    }
  } catch (error) {
    return error;
  }
};

