const CREATE_PROFILEDONOR_API = async (
  user: CreateProfileTypeDonor,
  token: string
) => {
  try {
    var myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${token}`);
    const formdata = new FormData();
    formdata.append("username", `${user.username}`);
    formdata.append("full_name", `${user.full_name}`);
    formdata.append("country", `${user.country}`);
    formdata.append("phone_number", `${user.phone_number}`);
    formdata.append("bio", user.bio);
    formdata.append("image", user.image ? user.image : "");
    formdata.append("donee_notification", `${user.donee_notification}`);
    formdata.append("account_activity", `${user.account_activity}`);
    formdata.append("donee_activity", `${user.donee_activity}`);
    formdata.append("achieved_goals", `${user.achieved_goals}`);
    formdata.append("new_followers", `${user.new_followers}`);
    formdata.append("NGO_role_assign", `${user.NGO_role_assign}`);

    const request = await fetch(`${API_URL}/update-user/`, {
      method: "PUT",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
      redirect: "follow",
    });

    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

const CREATE_PROFILENGOORDONE_API = async (
  user: CreateProfileTypeNgoOrDonee,
  token: string
) => {
  try {
    const formdata = new FormData();
    if (user.profile_type === "DONEE") {
      formdata.append("ngo_profile_id", `${user.ngo_profile_id}`);
      formdata.append("id_front", user.id_front ? user.id_front : "");
      formdata.append("id_back", user.id_back ? user.id_back : "");
    } else {
      user.certification_incorporation?.map((item) =>
        formdata.append("certification_incorporation", item)
      );
    }
    formdata.append("username", `${user.username}`);
    formdata.append("full_name", `${user.full_name}`);
    formdata.append("email", `${user.email}`);
    formdata.append("country", `${user.country}`);
    formdata.append("phone_number", `${user.phone_number}`);
    formdata.append("bio", user.bio);
    formdata.append("image", user.image ? user.image : "");
    user.sdgs.map((sd) => formdata.append("sdgs", `${sd}`));
    formdata.append("donee_notification", `${user.donee_notification}`);
    formdata.append("account_activity", `${user.account_activity}`);
    formdata.append("donee_activity", `${user.donee_activity}`);
    formdata.append("achieved_goals", `${user.achieved_goals}`);
    formdata.append("new_followers", `${user.new_followers}`);
    formdata.append("NGO_role_assign", `${user.NGO_role_assign}`);
    formdata.append("profile_type", `${user.profile_type}`);

    const request = await fetch(`${API_URL}/create-profile/`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
      redirect: "follow",
    });

    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

