const CREATE_GOAL_API = async (goal: CreateGoalType, token: string) => {
  try {
    const formdata = new FormData();
    formdata.append("title", `${goal.goalname}`);
    formdata.append("short_description", `${goal.shortGoal}`);
    formdata.append("full_description", `${goal.full_description}`);
    formdata.append("buying_item", `${goal.product}`);
    formdata.append("online_source_url", `${goal.productlink}`);
    formdata.append("image", goal.file ? goal.file : "");
    formdata.append("unit_cost", `${goal.unitcost}`);
    formdata.append("total_unit", `${goal.unitnumber}`);
    formdata.append("profile", `${goal.profile}`);
    goal.sdgs.map((sd) => formdata.append("sdgs", `${sd}`));
    if (goal.videofile !== null) {
      goal.videofile.map((file) => formdata.append("media", file));
    }

    const request = await fetch(`${API_URL}/create-goal/`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
      redirect: "follow",
    });

    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

