const CREATE_PAYMENT_API = async (data: MakeDonationProps, token: string) => {
  try {
    let reqData;
    // data.dedicated_gift_message === "" ||
    if (data.dedicated_message === "") {
      reqData = {
        goal: data.goal,
        amount: data.amount,
        is_dedicated: data.is_dedicated,

        is_gift_dedicated: data.is_gift_dedicated,
      };
    } else {
      reqData = {
        goal: data.goal,
        amount: data.amount,
        is_dedicated: data.is_dedicated,
        dedicated_message: data.dedicated_message,
        is_gift_dedicated: data.is_gift_dedicated,
        // dedicated_gift_message: data.dedicated_gift_message,
        dedicated_name: data.dedicated_name,
        dedicated_email: data.dedicated_email,
      };
    }

    const request = await fetch(`${API_URL}/create/payment/`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(reqData),
    });
    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

