const FOLLOW_UNFOLLOW_API = async (
  follow_type: string,
  followID: number,
  token: string
) => {
  try {
    const data =
      follow_type === "profile-follow"
        ? JSON.stringify({
            follow_profile: followID,
          })
        : JSON.stringify({
            follow_user: followID,
          });
    const request = await fetch(
      `${API_URL}/${
        follow_type === "profile-follow" ? "profile-follow" : "user-follow"
      }/`,
      {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: data,
      }
    );
    const response = await request.json();

    return response;
  } catch (error) {
    return error;
  }
};

