import { Typography, Grid, Container, Divider } from "@material-ui/core";
import FAQIMAGE from "../../../../assets/faq2.png";
import React from "react";
import { Box } from "@material-ui/system";
import { styled } from "@material-ui/styles";

const Faq = () => {
  return (
    <Container>
      <Grid container flexDirection="row" justifyContent="space-around">
        <Grid item md={4}>
          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            minHeight="100vh"
          >
            <img src={FAQIMAGE} style={{}} />
          </Box>
        </Grid>
        <Grid item md={6}>
          <FAQTITLE variant="h3">FAQ</FAQTITLE>

          <div style={{ paddingTop: 10 }}>
            {data.map((item) => (
              <div>
                <ContainerDiv>
                  <Question>{item.quen}</Question>
                  <Answer>{item.answer}</Answer>
                </ContainerDiv>
                <Divider />
              </div>
            ))}
          </div>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Faq;

const FAQTITLE = styled(Typography)(() => ({
  fontSize: 40,
  color: "#212529",
  width: 250,
  height: 50,
}));

const ContainerDiv = styled("div")(() => ({
  paddingTop: 5,
  paddingBottom: 5,
}));

const Question = styled(Typography)(() => ({
  fontSize: 20,

  fontWeight: 600,
  letterSpacing: 1.2,
}));
const Answer = styled(Typography)(() => ({
  fontSize: 12,
  fontWeight: 200,
  letterSpacing: 1.2,
}));

const data = [
  {
    quen: "What is Donee?",
    answer:
      "Donee is a donation platform where certified NGOs and their beneficiaries can easily showcase their causes through 20-second videos and receive donations from people around the world.",
  },
  {
    quen: "How many goals can my NGO create?",
    answer:
      " NGOs can create an unlimited number of goals, tip: remember to constantly update your goals with new 20 second videos that strengthen your storytelling.",
  },
  {
    quen: "Do my goals in Donee have a time limit to be completed?",
    answer:
      "No, there is no time limit to complete an objective. Your objective will be active until it is completed or until you decide to cancel or pause it. Tip: use the share button to reach your community by different means quickly and easily. Remember that your existing community is a great medium to replicate your message and attract new donors to your goals.",
  },
  {
    quen: "How do I withdraw funds from my Donee Account?",
    answer:
      "Once your goal starts collecting donations online, you can request to withdraw the money whenever you want, withdrawing money does not affect your goal progress bar. It can take 3 to 6 business days for the money to reach the bank account.",
  },
  {
    quen: "How does Donee keep donation money safe?",
    answer:
      "What technology or procedures do we use to provide security in transactions?",
  },
  {
    quen: "Can only nonprofits create goals in Donee?",
    answer:
      "Yes, organizations must be registered in their home countries as Non-profit organizations in order to create goals in Donee. However, certified NPO’s can invite individuals and will be recognized as beneficiaries of the NPO, allowing them to create their goals under the endorsement of the organization.",
  },
  {
    quen: "Does it cost anything to create goals in Donee? ",
    answer:
      "While creating goals and sharing them is completely free, when a goal is created, we add a 5% service fee + 2.9% processing fee to the total amount the NGO wants to raise. this amount is charged when the goal is completed or the money is withdrawn. To be clear, the NGO will receive 100% of what it needs to raise and the multiple donors will cover the cost of the service + the cost of processing that was added to the total amount of the goal.",
  },
  {
    quen: "What happens if the goal is not reached?",
    answer:
      "No problem, with Donee every donation you receive is yours to keep, your goal will be active as long as it is completed or until it is paused.",
  },
  {
    quen: "What payment gateways does Donee support?",
    answer: "You can receive your donations or make them through Paypal.",
  },
  {
    quen: "What are the video and photo formats supported by Donee?",
    answer:
      "Accepted video formats are: .MOV and .MP4 Accepted photo formats are: .HEF, JPEG, PNG and GIF.",
  },
];
