import { Card, Typography, Stack } from "@material-ui/core";
import React from "react";

import { styled } from "@material-ui/core/styles";

import { customShadows } from "../../../../../../theme/shadows";
const AverageDone = (
    {average_goal_conversion_rate}: any
  ) => {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={2}
        p={3}
      >
        <Value>{average_goal_conversion_rate}%</Value>
        <Label>Average Goal convertion rate</Label>
      </Stack>
    </RootStyle>
  );
};

export default AverageDone;

const RootStyle = styled(Card)(() => ({
  boxShadow: customShadows.z24,
  height: 156,
  color: "white",
}));

const Value = styled(Typography)(() => ({
  height: 40,
  fontSize: 32,
  fontWeight: "bold",
  color: "#343A40",
}));

const Label = styled(Typography)(() => ({
  height: 25,
  width: 150,
  fontSize: 20,
  fontWeight: "normal",
  color: "#6C757D",
  textAlign: "center",
}));
