import React, { useEffect, useState, useRef } from "react";
import { styled } from "@material-ui/styles";
import { Box } from "@material-ui/system";
import { Modal, Stack, Typography, TextField, Button } from "@material-ui/core";
import { useMutation } from "react-query";

import useAxios from "../../../../../../utils/useAxios";

import {
  AddFunction,
  CashoutProps,
  RootAppState,
} from "../../../../../../utils/typeHelpers";

import { MessageSuccess } from "../../../../../../components/AlertMessageSuccess";
import { MessageError } from "../../../../../../components/AlertMessageError";
import { useSelector } from "react-redux";

const RootStyle = styled(Stack)(() => ({
  top: "50%",
  left: "50%",
  position: "absolute",
  width: 400,
  backgroundColor: "white",
  padding: 20,
  transform: "translate(-50%, -50%)",
}));

const CashoutModal = ({
  open,
  handleClose,
  goal,
}: {
  open: boolean;
  handleClose: () => void;
  goal: number | null;
}) => {
  const axiosInstance = useAxios();
  const profileID = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.user_profile?.id
  );

  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const [cashOutData, setCashoutData] = useState<CashoutProps>({
    remark: "",
    profile: null,
    goal: goal,
  });

  const mutationCashout = useMutation(async (dataReq: CashoutProps) => {
    const response = await axiosInstance.post(`create/cashout/`, {
      remark: dataReq.remark,
      profile: dataReq.profile,
      goal: dataReq.goal,
    });
    return response;
  });

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  const { isSuccess, isError } = mutationCashout;

  useEffect(() => {
    if (profileID) {
      setCashoutData({ ...cashOutData, profile: profileID, goal: goal });
    }
  }, [profileID, goal]);

  useEffect(() => {
    if (isSuccess) {
      handleCreateSucess("Cashout Successful");
    } else if (isError) {
      handleCreateError("Cashout Failed");
    }
  }, [isSuccess, isError]);

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <RootStyle
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={2}
      >
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />
        <MessageSuccess
          children={(add: AddFunction) => {
            refSuccess.current = add;
          }}
        />
        <Typography id="modal-modal-title" variant="h6" component="h2">
          Cashout Now
        </Typography>
        <TextField
          fullWidth
          id="filled-basic"
          label="Remark"
          variant="outlined"
          value={cashOutData.remark}
          onChange={(e) =>
            setCashoutData({ ...cashOutData, remark: e.target.value })
          }
        />

        <Button
          onClick={() => {
            if (cashOutData.remark !== "") {
              mutationCashout.mutateAsync(cashOutData);
              handleClose();
            } else {
              handleCreateError("Remark can't be empty");
            }
          }}
          variant="outlined"
        >
          Cashout
        </Button>
      </RootStyle>
    </Modal>
  );
};

export default CashoutModal;
