import React, { useEffect, useState, useRef } from "react";
import { styled } from "@material-ui/styles";
import { Box } from "@material-ui/system";
import {
  Modal,
  Stack,
  Typography,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@material-ui/core";
import { useMutation } from "react-query";

import useAxios from "../../../../../../utils/useAxios";

import {
  AddFunction,
  CashoutProps,
  DropDownMenuProps,
  RootAppState,
} from "../../../../../../utils/typeHelpers";

import { MessageSuccess } from "../../../../../../components/AlertMessageSuccess";
import { MessageError } from "../../../../../../components/AlertMessageError";
import { useSelector } from "react-redux";

const RootStyle = styled(Stack)(() => ({
  top: "50%",
  left: "50%",
  position: "absolute",
  width: 400,
  backgroundColor: "white",
  padding: 20,
  transform: "translate(-50%, -50%)",
}));

interface ReviewChangeStatusProps {
  remark: string;
  status: string;
}

const ReviewModal = ({
  open,
  handleClose,
  id,
  handleRefetch,
  OPTIONS,
}: {
  open: boolean;
  handleClose: () => void;
  id: number | null;
  handleRefetch: () => void;
  OPTIONS: DropDownMenuProps[];
}) => {
  const axiosInstance = useAxios();

  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const [statusData, setStatusData] = useState<ReviewChangeStatusProps>({
    remark: "",
    status: "ACCEPTED",
  });

  const statusChangeMutation = useMutation(
    async (dataReq: ReviewChangeStatusProps) => {
      const finalReqData =
        dataReq.remark === "" ? { status: dataReq.status } : dataReq;
      const response = await axiosInstance.put(
        `cashout-user-status-update/${id}/`,
        finalReqData
      );
      return response;
    }
  );

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  const { isSuccess, isError } = statusChangeMutation;

  useEffect(() => {
    if (isSuccess) {
      handleCreateSucess("Cashout Successful");
    } else if (isError) {
      handleCreateError("Cashout Failed");
    }
  }, [isSuccess, isError]);

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <RootStyle
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={2}
      >
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />
        <MessageSuccess
          children={(add: AddFunction) => {
            refSuccess.current = add;
          }}
        />
        <Typography id="modal-modal-title" variant="h6" component="h2">
          Cashout Now
        </Typography>
        <TextField
          fullWidth
          id="filled-basic"
          label="Remark"
          variant="outlined"
          value={statusData.remark}
          onChange={(e) =>
            setStatusData({ ...statusData, remark: e.target.value })
          }
        />
        <FormControl fullWidth>
          <InputLabel id="demo-simple-select-label">Select Status</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={statusData.status}
            label="Status"
            onChange={(e) =>
              setStatusData({ ...statusData, status: e.target.value })
            }
          >
            {OPTIONS.map((item: DropDownMenuProps, i) => (
              <MenuItem key={`$i`} value={item.value}>
                {item.label}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <Button
          color="info"
          onClick={async () => {
            if (
              statusData.status === "REJECTED_BY_ADMIN" ||
              statusData.status === "REJECTED_BY_NGO"
            ) {
              handleCreateError("Remark can't be empty");
            } else {
              await statusChangeMutation.mutateAsync(statusData);
              await handleClose();
              await handleRefetch();
            }
          }}
          variant="outlined"
        >
          CHANGE STATUS
        </Button>
      </RootStyle>
    </Modal>
  );
};

export default ReviewModal;
