// material
import { styled } from "@material-ui/core/styles";
import { Card, Stack, Typography } from "@material-ui/core";
import { Icon, IconifyIcon } from "@iconify/react";

import { customShadows } from "../../../../../../theme/shadows";

const RootStyle = styled(Card)(() => ({
  padding: 20,
  boxShadow: customShadows.z12,
  height: 189,
  color: "white",
}));

const Label = styled(Typography)(() => ({
  fontSize: 20,
  height: 24,
  fontWeight: "normal",
  color: "#6C757D",
}));

const Value = styled(Typography)(() => ({
  fontSize: 32,
  height: 40,
  fontWeight: "bold",
  color: "#000000",
}));

const TypographyBody = styled(Typography)(() => ({
  color: "#CED4DA",
  fontsize: 14,
}));
// ----------------------------------------------------------------------
interface YourBalanceProps {
  balance: string;
  balanceToday: string;
  icon: IconifyIcon;
  iconColor: string;
}
export default function YourBalance({
  balance,
  balanceToday,
  icon,
  iconColor,
}: YourBalanceProps) {
  return (
    <RootStyle>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={3}
      >
        <Label>Your Balance</Label>

        <Value>{balance}</Value>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={0}
        >
          <Icon icon={icon} width={16} height={16} color={iconColor} />
          <TypographyBody variant="body1">${balanceToday} Today</TypographyBody>
        </Stack>
      </Stack>
    </RootStyle>
  );
}
