import { Icon } from "@iconify/react";
import dollarFilled from "@iconify/icons-ant-design/dollar-circle-fill";
// material
import { styled } from "@material-ui/core/styles";
import { Card, Typography } from "@material-ui/core";
// utils
import { fShortenNumber } from "../../../../../../utils/formatNumber.js";

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: theme.shadows[3],
  textAlign: "center",
  padding: theme.spacing(5, 0),
  backgroundColor: theme.palette.grey[0],
}));

const IconWrapperStyle = styled("div")(({ theme }) => ({
  margin: "auto",
  display: "flex",
  borderRadius: "50%",
  alignItems: "center",
  width: theme.spacing(6),
  height: theme.spacing(6),
  justifyContent: "center",
  marginBottom: theme.spacing(2),
  color: theme.palette.primary.light,
}));

// ----------------------------------------------------------------------

export default function CardWallet({ value, label }) {
  return (
    <RootStyle>
      <IconWrapperStyle>
        <Icon icon={dollarFilled} width={50} height={50} />
      </IconWrapperStyle>
      <Typography variant="h3">${fShortenNumber(value)}</Typography>
      <Typography variant="subtitle2" sx={{ opacity: 0.72 }}>
        {label}
      </Typography>
    </RootStyle>
  );
}
